.LoadingOverlay {
	position: fixed; 
	display: block;
	width: 100%;
	height: 100%; 
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.9);
	z-index: 2;
}

.LoadingOverlay-disc {
	position: absolute; 
	top: 45%;
	left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

  height: 50px;
  width: 50px;
  background-color: rgb(207, 36, 36);
	border-radius: 50%;
	border-style: solid;
	border-color: black;
	border-width: 4px;
	display: inline-block;
	-webkit-animation: strobe 2s ease-in-out infinite alternate;
	        animation: strobe 2s ease-in-out infinite alternate;
}

@-webkit-keyframes strobe {
  0% { background-color: rgb(207, 36, 36); }
  100% { background-color: rgb(44, 30, 172); }
}

@keyframes strobe {
  0% { background-color: rgb(207, 36, 36); }
  100% { background-color: rgb(44, 30, 172); }
}

.LoadingOverlay-text{
  position: absolute;
  top: 50%;
	width: 100%;
	font-size: 2.5em;
  color: white;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	overflow: hidden;
	text-align: center;
}

.App {
  height: 100%;
}

